.c-005{
    padding:50px 0;
    h3{
        margin: 0;
        padding-bottom: 38px;
        @extend %font-normal;
        font-weight: 300;      
        line-height: 1.33;      
        letter-spacing: -0.4px;
        
    }
    p, a{
        color:$color-black-alpha;
        @include respond-to('xxs'){
            font-size: 14px;
        }
        &:after{
            @extend %clear;  
          }
    }
    .item{
        padding-bottom: 30px;
        @include respond-to('md'){
            padding-bottom: 23px;
        }
        &:last-child{
            padding-bottom: 0;
        }
    }
}//c-005