.registration{
    form{
        padding-top: 20px;
        @include respond-to('md'){
            padding-top: 50px;
            #formpassword{
                padding-top: 0;
            }
        }
        
        i.tooltipMark{
            font-size:20px;
            line-height: 35px;
            cursor:pointer;
            @include respond-to('md'){
                font-size:30px;
                line-height: 35px;
            }
        }
        &#registration, &#formprofile{
            margin-top: 35px;
            border-top:1px solid lighten($color-gray-plain, 10%);
            @include respond-to('md'){
                margin-top: 70px;
            }
            .btn{
                padding: 18px;
            }
            .warning{
                font-size:14px;
                line-height: 20px;
                color: darken($color-blue,10%);
                display: none;
                padding:5px 0;
            } 
            .dob-error{
                @extend %form-errors;
                display: none;
            }           
        }//registration
    }//form
    ul{
        &.checked{
            li{
                &:before{
                    display: none;
                }
                &.no{
                    i{
                        display: none;
                    }
                }
                &.ok{
                    i{
                        display: inline-block;
                        zoom:1;
                        margin-right: 10px;
                        color:$color-aquamarine;
                    }
                }
                
            }//li
        }//checked
    }//ul
    .avatar-panel{
        position: relative;
        height: 150px;
        @include respond-to("md"){
            text-align: right;
        }
        .btn-wrapper{
            position: absolute;
            left: 150px;
            top:30px;
            @include respond-to("md"){
                left: auto;
                right: 150px;
                
            }
        }
        img{
            width: 120px;
            height: 120px;
            border-radius: 50%;
            position: absolute;
            left:0;
            @include respond-to("md"){
                left: auto;
                right: 0;
            }   
        }
    }//avatar-panel
}//registration

.profile-forms{
    label:not(.error){
        line-height: 40px;
        @include respond-to('md'){
            display: block;
        }
        &.custom-checkbox{
            margin-top: 13px;
            float:left;
            &:after{
                @extend %clear;
            }
            @include respond-to('md'){
                float: right;
            }
        }
        @include respond-to('md'){
            text-align: right;
        }
    }
}


