ul.navbar-nav{
	margin:0;
	padding: 0;
	li{
		margin:0;
	}
}

section.nav-menu{
	@extend %transition;
	position: relative;
	&:after{
		@extend .clearfix;
	}
	.bottom{
		margin-top: 50px;
		text-align: center;
		a{
			display: inline-block !important;
			width: auto !important;
		}
		span{
			padding: 0 10px;
			display: inline-block;
			zoom:1;
		}
	}//bottom
}

.navbar-brand {
	width: 109px;
	height: 24px;
	display: block;
	margin: 0 auto;
	padding: 22px 0 0 0;
	float:none;
	@include respond-to('md'){
		padding: 45px 0;
		width: 150px;
	}
	img{
		width: 100%;
		max-width: 100%;
	}//img
}//navbar-brand


.navbar.navbar-default{
	background: transparent;
	border: none;
	z-index: 4;
	padding-bottom: 20px;
	margin-bottom: 0;
	@extend %box-shadow-bottom;
	.navbar-collapse{
		border: none;
	}

	.navbar-nav > li {
		> a{
			color:$color-blue;
			font-size: em(16);
			text-transform: capitalize;
			padding: 30px 0 30px 0;
			border-bottom: 1px solid darken($color-gray,5%);
			@extend %transition;
			&:hover{
				text-decoration: none;
				color:$color-blue;
				@include respond-to('md'){
					> span{
						text-decoration: underline;
						text-underline-position: under;
					}
				}
			}
			.svg-inline--fa{
				font-size:em(10);
				float: right;
				@include respond-to('md'){
					float: none;
				}
			}
			@include respond-to('md'){
				padding: 60px 0 0 0;
				border:none;
			}
		}
		&.open{
			> a{
				text-decoration: none;
				color:$color-blue;
				background: none;
				@extend %transition;
				&:hover, &:focus{
					color:$color-blue;
				}
				@include respond-to('md'){
					> span{
						text-decoration: underline;
						text-underline-position: under;
					}
				}
			}//a
		}
	}
	.navbar-toggle{
		border:none;
		float:left;
		margin-top: 19px;
		margin-bottom: 0;
		&:hover, &:focus {
			background: none;
		}
		.icon-bar {
			background-color: $color-blue;
		}
		&:after {
			content: "Close";
			float: right;
			position: absolute;
			right: -30px;
			top: 6px;
			font-size: 12px;
			color: $color-blue;
		}
		.top-bar {
			transform: rotate(45deg);
			transform-origin: 10% 10%;
			@extend %transition;
		  }
		  .middle-bar {
			opacity: 0;
			@extend %transition;
		  }
		  .bottom-bar {
			transform: rotate(-45deg);
			transform-origin: 10% 90%;
			@extend %transition;
		  }
		  &.collapsed {
			&:after {
				content: "Menu";
			}
			.top-bar {
			  transform: rotate(0);
			}
			.middle-bar {
			  opacity: 1;
			}
			.bottom-bar {
			  transform: rotate(0);
			}
		  }
	}//navbar-toggle

}//navbar-default

