.nav.navbar-nav.navbar-right{
	font-size: em(14);
	color:$color-text-dark;
	position:relative;
	@include respond-to("md"){
		//max-width: 230px;
		max-width: 255px;
	}

	.navbar-text{
		font-size: em(14);
		font-weight: 700;
		a{
			color:rgba(0,0,0,0.5);
			@extend %transition;
			&:hover{
				text-decoration: none;
				color:darken($color-text-dark,5%);
				span{
					text-decoration: none;
					color:darken($color-text-dark,5%) !important;
				}
			}
		}//a
	}
	li{
		&.dropdown{
			float: right;
		}
		a{
			margin:0;
			padding:0;
			text-decoration: none;
		}
		/*.search {
			float: none;
			position:relative;
			clear: both;
			input{
				width: 50%;
				float: right;
				border:none;
			}
			i{
				position: absolute;
				top:5px;
				right: 22px;
			}
		}//search*/
	}//li

	.divider{
		width: 1px;
		height: 35px;
		background: darken($color-gray,20%);
		margin-top: 15px;
		text-align: center;
	}
	.name{
		width: 58px;
		display: inline-block;
		vertical-align: middle;
		text-align: center;
		@extend %ellipsis;
	}

	&.signed-in{

		.avatar{
			position: relative;
			display: inline-block;
			zoom:1;
			img {
				display:inline-block;
				width: 35px;
				height: 36px;
				border-radius: 50%;
				vertical-align: middle;
				margin-left: 10px;
			}
			i.fa{
				font-size:em(12);
				margin-left: 5px;
				&.fa-circle{
					position: absolute;
					top:0;
					right: 15px;
					font-size: 12px;
				}
			}//fa
		}//avatar
	   // .default-avatar,
		.text-message{
			display:none;
		}


		.left-panel{
			margin-top: 14px;
			margin-right: 20px;
			max-width: 60px;
			max-height: 30px;
			img{
				display: block;
				max-width: 100%;
				height: auto;
			}//img
		}
		li.dropdown{
			.fa-chevron-up{
				display: none;
			}
			&.open{
				.fa-chevron-up{
					display: inline-block;
				}
				.fa-chevron-down{
					display: none;
				}
			}//open
		}//li.dropdown
	}//signed-in

	&.signed-out{

		.left-panel{
			margin-top:6px;
		}
		/*.search input{
			width: 41%;
		}*/
		.avatar{
			i{
				display: none;
			}
		}//avatar
		#login-dp,
		//.avatar img,
		.left-panel img{
			display: none;
		}
	}//signed-in
	&.no-navbar-right{
		display:none;
	}
}//.nav.navbar-nav.navbar-right

.get-started{
	padding-bottom: 50px;
	margin-bottom: 40px;
	@extend .text-alpha;
	p{
		@extend .text-alpha;
	}
}


