#login-top-mobile-button{
	list-style-type: none;
	position: absolute;
	top:15px;
	right: 20px;
	@include respond-to('sm'){
		top:17px;
		right: 32px;
	}
	cursor: pointer;
	span{
		display: block;
		&.avatar{
			position: relative;
			i.fa.fa-circle.red {
				position: absolute;
				right: 0px;
				top: 0;
				font-size: 12px;
			}
		}
	}//span
}

div#row {
	margin-left: -15px;
	margin-right: -15px;
	@extend %box-shadow-top;
	@extend %box-shadow-bottom;
	@include respond-to('sm'){
		margin-left: initial;
		margin-right: initial;
	}
}

#mobile-profile{
	display: none;
	width: 100%;
	margin-top: 22px;
	padding:50px;
	list-style-type: none;
	position: relative;
	@include top-traingle-menu($top:3px,$left:initial,$right:-3px,$bottom:initial);
	@include respond-to('sm'){
		-moz-box-shadow: 0 0 3px  rgba(0, 0, 0, 0.2);
		-webkit-box-shadow: 0 0 3px  rgba(0, 0, 0, 0.2);
		box-shadow: 0 0 3px  rgba(0, 0, 0, 0.2);
		@include top-traingle-menu($top:3px,$left:initial,$right:-4px,$bottom:initial);
	}
	.get-started{

	}
	a:not(.btn){
		@extend .text-alpha;
		font-size:em(15);
		display: block;
		font-weight: bold;
		width: 100%;
		&:empty{
			display: none;
		}
	}
	.user-profile{
	   @extend %profile-links;
	   > a{
			display: block;
			float: none;
			&:after{
				@extend .clearfix;
			}
	   }
	}//user-profile
	.bottom{
		margin: 50px 0 30px 0;
	}


}//Mobile-profile




