.mega-dropdown {
	position: static !important;
	width:100%;
	@include respond-to('md'){
		width:auto;
		padding:0 20px;
	}
	>a.dropdown-toggle{
		&:after{
			content:"\f054";
			font-size:em(10);
			float: right;
			font-family: fontAwesome;
			@include respond-to('md'){
				content:"\f078";
				float: none;
				margin-left: 3px;
			}
		}
	}//dropdown-toggle
	&.open{
		>a.dropdown-toggle{
			&:after{
				content:"\f078";
				@include respond-to('md'){
					content:"\f077";
				}
			}
		}//a
	}//open
	&.no-icon{
		>a.dropdown-toggle{
			&:after{
				content:" ";
			}
		}//a
	}//no-icon
}//mega-dropdown
.navbar-default .navbar-nav{
	.mega-dropdown-menu {
		padding: 20px 0px;
		border-bottom: 1px solid darken($color-gray,5%);
		/*-moz-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
		-webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
		box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);*/
		@include respond-to("md"){
			border: none;
		}
		> li {
			> a{
				padding:20px 0;
				color: rgba(0,0,0,0.5) !important;
				font-size: em(16);
				&:hover{
					background: none;
					color:black !important;
				}
			}
		}
			.dropdown-submenu{
				> a{
					color: rgba(0,0,0,0.5) !important;
					font-size: em(16);
					line-height: em(23);
					text-transform: capitalize;
					padding: 25px 0 25px 0px;
					background: none;
					&:hover,
					&:focus{
						outline: none;
					}

					display: block;
					&:after{
						content: "\f054";
						font-size: .625em;
						float: right;
						font-family: fontAwesome;
					}
					&.open{
						&:after{
							content: "\f078";
							font-size: .625em;
							float: right;
							font-family: fontAwesome;
						}
					}
				}//a
				.dropdown-menu{
					color: rgba(0,0,0,0.5);
					border: none;
					box-shadow: none;
					> li{
						a{
							color: rgba(0,0,0,0.5);
							padding-top: 20px;
							padding-bottom: 20px;
						}
					}//li
				}//dropdown-menu
			}//.dropdown-submenu

	}//mega-dropdown-menu
}

.mega-dropdown-menu > .container > li > ul {
	padding: 0;
	margin: 0;
}
.mega-dropdown-menu > .container > li > ul > li {
	list-style: none;
}


.mega-dropdown-menu > .container > li > ul > li > a,
.mega-dropdown-menu > .container > li > ul > li  {
	display: block;
	clear: both;
	font-weight: normal;
	color: rgba(0,0,0,0.5);
	font-size: em(15);
	line-height: em(23);
	white-space: normal;
	@extend %transition;
	&:hover{
		background: none;
		color:black;
	}
	@include respond-to('md'){
		padding: 0;
	}
}

.mega-dropdown-menu > .container > li > ul > li{
	padding: 10px 0;
}

.mega-dropdown-menu > .container > li ul > li > a:hover, .mega-dropdown-menu > .container > li ul > li > a:focus {
	@extend %transition;
	&:hover{
		color:black;
		text-decoration: none;
	}
}
.mega-dropdown-menu .container li.dropdown-header {
	color:black;
	font-size: em(14);
	line-height: em(23);
	padding:15px 0;
	//font-weight: bold;
}

.menu-divider{
	padding:20px 0;
}

