/* CUSTOM STYLES FOR SLICK SLIDER */
.slider{
    margin-top: 50px;
    .slick-dots{
        bottom: 0px;
        padding: 50px 0 0 0;
        position: relative;
        li{
            width: auto;
            height: auto;
            button{
                border-radius: 50%;
                height: 10px;
                /*margin-top: 100px;*/
                width: 10px;
                @extend %transition;
                text-indent: -1000000px;
                &:before{
                    width: auto;
                    height: auto;
                }
            }            
            &.slick-active{
                button{
                    &:after{
                        content:"";
                    }
                }//button
            }//slick-active
        } //li
    }//slick-dots
    .slick-arrow{
        background: white;
        border-radius: 50%;
        height: 50px;
        width: 50px;
        z-index: 2;
        display: none !important;
        @extend .box-shadow-complete;
        @include respond-to('md'){
            display: block !important;
        }
        
        &:hover,
        &:focus{
            //background:darken(white,5%);
            background:lighten($color-blue,15%);
            &:before{
                color:white;
            }
        }
        &:before{
            color:$color-blue;
            display: inline-block;
            font-family: fontAwesome;
        }
        &.slick-next {
            &:before {
                content: "\f054";
                margin-right: -5px;
            }
        }
        &.slick-prev{
            &:before {
                content: "\f053";
                margin-left: -5px;
            }
        }//prev
        &.slick-disabled{
            display: none !important;
        }    
    }//.slick-arrow 
}//slider

.pagingInfoSlider{
    color:$color-black-alpha;
    text-align: center;
    display: block;
    font-weight: 500;
    padding: 50px 0;
}

.slider-section{
    &.bg-blue{
        .pagingInfoSlider{
            color:white;
        }
    }
}//slider-section

.slick-track{
    display: flex;
    .slick-slide {
        display: flex;
        > div {
            display: flex;
        }
    }//slide
}//slick-track