.search-results{
    #search-big{
        position: relative;
        padding-bottom: 30px;
        input{
            font-size: 20px;
            height: 40px;
            color:black;
            @include respond-to("md"){
                font-size: 36px;
                height: 60px;
            }
        }
        i{
            position: absolute;
            color:$color-black-alpha;
            right: 10px;
            top: 12px;
            cursor:pointer;
            @include respond-to("md"){
                top:22px;
            }
            &.fa-times{
                right: 40px;
                display: none;
            }
        }
    }
    .no-results{
        padding-top:70px; 
    }
    .results-count{
        @extend %font-normal;
        font-size: 13px;
        line-height: 1.69;
        color:$color-black-alpha;
        padding-top: 20px;
        span{
            padding-right:5px; 
        }
    }
    
    .item{
        margin:50px 0;
        display: block;
        color:$color-black-alpha;
        &.offer{
            @extend .box-shadow-complete;
            .content{
                padding: 30px;
            }
        }
        a{
            &:hover{
                text-decoration: none;
            }
        }
        &:first-child{
            //padding-top: 0;
        }
        &:last-child{
            margin-bottom: 0;
        }
        img{
            float:left;
            padding-right: 20px;
            //padding-bottom: 30px;
            @include respond-to("xxs"){
                float: none;
                clear: both;
                object-fit: cover;
                width: 100%;
                height: auto;
                padding-right: 0;
                padding-bottom: 10px;
            }
        }
       
        p{
            font-size: 16px;
            line-height: 1.44;
            color:$color-black-alpha;
            float: none;
            clear: both;
            padding-top: 5px;
            &.eye-brow{
                color:$color-blue;
                @extend %font-normal;
                font-size: 12px;
                line-height: 1.33;
                text-transform:uppercase; 
                width: 100%; 
            }
            &.title{
                @extend %font-normal;
                font-size: 18px;
                font-weight: bold;
                line-height: 1.28;
                color:#08202d;
                width: 100%; 
            }
            @include respond-to("xxs"){
                //float: none;
                //clear: both;
                //padding-top: 20px;
            }
        }//p
        .item-text{
            padding-top: 0;
            float: left;
            width:100%;
            @include respond-to("sm"){
                width: 74%;
            }
        }
        
        .content:after {
            content: "";
            display: table;
            clear: both;
        }

    }//item
}//search-results