
.tooltip{
    width: max-content;
    .tooltip-{
        &inner {
            max-width: 335px;
            padding: 30px;
            color: $color-white-alpha;
            text-align: center;
            background-color: $color-footer;
            border-radius: 4px;
            a{
                color: $color-white-alpha;
                text-decoration: underline;
                text-underline-position: under;
                &:hover{
                    color: white;
                }
            }
        }//inner
        &arrow{
            border-top-color: $color-footer;  
        }
    }//TOOLTIP
}//tooltip
