$bg-colors: (white:white,aquamarine: $color-aquamarine, gray: $color-gray, red:$color-red, blue:$color-blue , dark-blue:$color-footer, green:$color-green, cyan:lighten($color-blue,25%));

@each $name, $color in $bg-colors {
  .bg-#{$name} {
    background: $color;
  }
  .#{$name}{
    color:$color;
  }
} //each

