.search{
    .loader{
        position: absolute;
        top: 7px;
        right: 32px;
        z-index: 9;
        .sk-circle{
            margin: 0;
            width: 20px;
            height: 20px;
        }
    }//loader
    &#desktop-search{
        //.easy-autocomplete{
            position: relative;
            float: right;
            width: 100% !important;
            text-align: right;
            margin-right: 15px !important;
            input{
                width: 80%;
                position: relative;
                text-align: center;
                border-color: #fff;
                border-radius: 4px;
                border-style: solid;
                border-width: 1px;
                @extend %transition;
                box-shadow: none;
                text-align: right;
                padding-right: 50px;
                padding-top: 5px;
                padding-bottom: 5px;
                &:focus,&:hover{
                    width: 100%;
                    text-align: left;
                    padding:5px 10px;
                    border-color: #ccc;
                    color: #555;
                    float: none;
                    @extend .box-shadow-complete;
                    + i{
                        //top: 8px;
                    }
                }
            }
            i{
                position: absolute;
                right: 0px;
                top: 0px;
                font-size: 16px;
                color:$color-black-alpha;
                cursor: pointer;
                width: 30px;
                height: 36px;
                //background: rgba(138, 59, 59, 0.35);
                text-align: center;
                line-height: 34px;
            }
        //}
    }//li#search
    .suggesstion-box {
        max-height: 200px;
        width: 100%;
        overflow-y: auto;
        position: absolute;
        right: 0;
        z-index: 9;
        border:none;
        background: white;
        //display: none;
        @extend .box-shadow-complete;
        a {
            width: 100%;
            display: block;
            margin: 0;
            padding: 10px 15px !important;
            border:none;
            text-align: left;
            &:hover{
                background: $color-gray;
            }
        }    
    }
    
    &#mobile-search{
        margin: 37px 0 30px 0;
        position: relative;
        &:after,
        &:before{
            @extend %clear;
        }
        @include respond-to('md'){
         display: none;
        }
        #search-mobile{
            float: right;
            margin-right: 30px;
            width: 80%;
            /*background: $color-gray;*/
            height: 60px;
            font-size: 17px;
            border: none;
            box-shadow: none;
            text-align: right;
            @extend %transition;
            &:hover,
            &:focus{
                width: 100%;
                text-align: left;
                margin-right: 0;
                @extend .box-shadow-complete;
                background: white;
                & + i.fa-search{
                    display: block;
                    
                }
            }
        }
        i.fa-search{
            position: absolute;
            top: 0;
            right: 0;
            display: block !important;
            color:$color-black-alpha;
            cursor: pointer;
            display: none;
            width: 40px;
            height: 61px;
            //background: #00000042;
            text-align: center;
            line-height: 61px;
        }
    }//mobile-search
}//search


