.btn {
    //height: 60px;
    //line-height: 60px;
    @extend %btn;

    &-large {
        width: 100%;
        margin-bottom: 20px;
        @include respond-to('md'){
            /*min-width: 104px;
            max-width: 240px;
            width: 240px;*/
        }
    }
    
    &-secondary {
        min-width: 104px;
        max-width: 204px;
        width: 204px;
        font-size: em(18);
        margin:40px 0;
        &:hover{
            a{
                color:black;
            }    
        }        
        a{
            color:black;
        }
    }

    &-orange {
        background: $color-orange;
        color: white;

        &:hover,
        &:focus {
            background: darken($color-orange, 20%);
            color: white;
        }
    }
    &-blue {
        color: #fff !important;
        background-color: $color-blue;
        border-color: $color-blue;
        &:hover{
            color: #fff !important;
            background-color: darken($color-blue,20%);
        }
        a{
            color: #fff !important;
        }
    }
    &-cyan {
        background-color: $color-cyan;
        border-color: $color-cyan;
        color: #fff !important;
        &:hover{
            background-color: darken($color-cyan,20%);
            color: #fff !important;
            a{
                color: #fff !important;
            }
        }
        a{
            color: #fff !important;
        }
    }
    &-dark-blue{
        background: $color-footer;
        color:white;
        &:hover{
            background: darken($color-footer,5%);
            color:white;
            a{
                color:white;
            }
        }
        a{
            color:white;
        }
    }
    &-aquamarine{
        background: $color-aquamarine;
        color:white;
        &:hover{
            background: darken($color-aquamarine,5%);
            color:white;
        }
    }
    &-bordered-white{
        background: transparent;
        border-color:white;
        color:white;
        &:hover,
        &:focus{
            background: white;
            color:black;
            a{
                color:black;
            }
        }
        a{
            color:white;
        }
    }
    &-white{
        background: white;
        color:black;
    }
    &-bordered-blue{
        background: transparent;
        border-color:$color-footer;
        color:$color-footer;
        &:hover,&.active{
            background: $color-footer;
            color:white;
        }
    }
    &-filters{
        padding:10px 20px;
        margin:10px 5px;        
        @extend %font-normal;
        font-size: 12px;
        font-weight: bold;
        &:hover,&:focus,&.active{
            font-size: 12px;
            padding:10px 20px;
        }
    }//filters
    &.disabled{
        @extend %disabled;
        a{
            @extend %disabled;
        }
    }
} //btn


.btn-group{
    &-top{
        position: absolute;
        top: 85px;
        width: 100%;
        text-align: center;
        z-index: 2;
        .btn{
            padding: 10px 20px;
            font-size: 10px;
        }
        
        @include respond-to('md'){
            position: absolute;
            top: 135px;
            .btn{
                padding: 10px 20px;
                font-size: 12px;
            }
        }//mediaquerie
    }//top
    &.pills{
        .btn{
            min-width: 90px !important;
            padding: 10px 30px;
            font-size: 12px;
        }
    }//on-off

}//btn-group

.btn-group-top.scEnabledChrome{
    top: 380px;
}//btn-group

