.c-027{
    .container {
        padding-right: 0;
        padding-left: 0;
        @include respond-to("md"){
            padding-right: 15px;
            padding-left: 15px;
            margin-right: auto;
            margin-left: auto;    
        }
        > .col-md-12{
            padding-right: 0;
            padding-left: 0;
            @include respond-to("md"){
                padding-right: 15px;
                padding-left: 15px;
            }
            #items{
                padding-right: 15px;
                padding-left: 15px;
                @include respond-to("md"){
                    padding-right: 0;
                    padding-left: 0;
                }
                .card{
                    img{
                        width: 100%;
                        max-width: 100%;
                        max-height: 170px;
                        -o-object-fit: cover;
                        object-fit: cover;
                        @include respond-to("md"){
                            display: block;
                            max-width: 100%;
                            height: auto;
                        }
                    }//img
                }//card
            }//items
        }//col-md-12        
    }//container


    .loader{
        //display: none;
        width: 100%
    }
   #filters{
        margin:30px auto 50px auto;
        overflow: hidden;    
        text-align: left;
        position: relative;
        > div {
            display: inline-block;
        }
        @include respond-to("md"){
            text-align: center;
            margin:30px auto 100px auto;
            width: 100%;
            max-width: 882px;
            height: 69px;
            overflow: hidden;
            &.open{
                height: auto;
                overflow: visible;
            }
        }
        
        a.btn{
            border-width:2px;
            @extend %ellipsis;
            display: inline-block;
            zoom:1;
            width: 137px;
        }//a        
        #more-filters{
            border-color:#abb7be;
            display: none;
            &:hover{
                border-color:$color-footer;
            }
            @include respond-to("md"){
                display: inline-block;
            }
        }
   }//filters
   #items{
       display: flex;
       flex-wrap: wrap;
       .card{
            display: flex;
            margin-bottom: 30px;
            h3{
                @extend %font-normal;
                font-size:18px;
                font-weight: bold;
                line-height: 1.28;
                margin-top: 15px;
            }
            p{
                color: $color-black-alpha;
                font-size:16px;
                @extend %font-normal;
                line-height: 1.44
            }
            .genre{
                @extend %genre;
                &:empty{
                    display: none;
                }
            }//genre
       }//card
   }//items
   .more-items{
        text-align: center;
        display: block;
        font-size: 21px;
        display: none;
        padding: 50px 0;
        &.show{
            display: block;
        }
    }//more-items 
    #mobile-filters{
        margin:50px 0 70px 25px; 
        a.btn{
            border-width:2px;
            @extend %ellipsis;
            max-width: 200px;
            display: inline-block;
            zoom:1;
            &:hover{
                background: transparent;
                border-color: #002c44;
                color: #002c44;
            }
        }//a        
        .active{
            position: relative;
            background: $color-footer;
            color:white;
            &:after{
                content:"\f00d";
                font-family: fontAwesome;
                font-size: 13px;
                color:white;
                position: absolute;
                top:0;
                right: 5px;
            }
        }
    }
}//c27

