section{
    &.headline-hero{
        //background: $color-blue;
        position:relative;
        color:white;
        padding:50px 0;
        @extend .full-width-image-bg;
        @include respond-to('md'){
            padding:110px 0;
        }
        &.big{
            @include respond-to('md'){
                padding:160px 0;
            }   
        }
        &.bg-blue{
            @include respond-to('md'){
                padding:80px 0;
            }   
        }
        h1,h3{
            color:white;
            letter-spacing: -1px;
        }
        h3{
            @extend %font-normal;
            font-weight: 200;
            line-height: 1.44;
            letter-spacing: 0;
            color:$color-white-alpha;
            font-size:16px;
            @include respond-to('md'){
                max-width: 754px;
                color:white;
                &.large{
                    width: 100%;
                    max-width: 100%;    
                }
            }

        }
    }//headline-hero
    &.header-image{
        padding:30px 0;
        padding-bottom: 0;
        &.overlap{
            padding: 0;
            margin-top: -40px;
            @include respond-to("md"){
                margin-top: -60px;
            }
        }
    }
}//section