.c-30{
    margin-bottom: 30px;
    @include respond-to("md"){
        display: flex;
    }
    > div[class*="col-"]{
        padding: 0;
        overflow: hidden;
    }
    > div > div[class*="col-"]{
        align-items: center;
    }
    > div > div[class*="col-"],
    > div[class*="col-"]{
        position: relative;
        @include respond-to('md'){
            min-height: 560px;
            display: flex;
            /*flex-wrap: wrap;*/
            align-content: center;
        }
    }
    .video-container{
        padding:30px 0;
        @include respond-to('md'){
            padding:0;
        }
        p{
            color:$color-white-alpha;
            line-height: 1.44;
            padding: 20px 40px;
        }
    }
    h2{
        width: 100%;
        display: block;
    }
    .bg-blue{
        h2{
            &.text-white{
                font-size: 30px;
                color:white;
                margin: 0;
                padding: 100px 0;
                @include respond-to("md"){
                    font-size: 60px;
                    padding: 0;
                }
            }
        }//h2
    }//bg-blue
    
    .text-panel{
        padding: 20px;
        display: flex;
        flex-wrap: wrap;
        @include respond-to('md'){
            padding: 50px;
            display: block;
        }
        > .text-panel{
            padding: 0px;
        }
        ul{
            li{
                color:$color-black-alpha;
                a{
                    color:$color-black-alpha;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }
        }//ul
        &.numbered{
            div.number{
                display: block;
                float:left;
                width: 10%; 
            }//
            div.text{
                @extend .number; 
                width: 90%;        
            }
            .header{
              font-size: em(30);
              width: 100%;
            }//header
            .subhead{
                padding:10px 0;
                font-size: em(16);
            }
            p{
                line-height: 1.5;
            }
            a{
                padding: 5px 0;
                display: block;
                font-size: 16px;                        
                color: $color-blue;
            }
            ul{
                list-style-type: none;
                padding: 0;
                margin:20px 0;
                li{
                    margin:5px 0;
                    a{
                        padding: 0;
                        display: block;
                        font-size: 16px;                        
                        color: $color-blue;
                    }
                }
            }//ul
            
        }//numbered
    }//text-panel 
    .logo-panel{
        padding: 0 40px 40px 40px;
        @include respond-to("sm"){
            padding: 0 40px;
        }
        img{
            padding: 30px 0;
            
            @include respond-to("md"){
                padding: 0 0 100px 0;
            }
        }//img
        
    }//logo-panel
    .caption{
        font-size: em(14);
        padding:20px 30px;
        @extend .text-alpha-white;
    }
   
}//c-30



video {
    max-width: 100%;
    vertical-align: middle;
}

//plyr
button{
    &.plyr__control--overlaid {
        background:none;
        color: transparent;
        top:42%;
        @include respond-to('md'){
            top:42%;
        }
        &:hover{
            background:none;
            color: transparent;
        }
        
        &:after{
            content:"\f144";
            color: white;
            font-size: 60px;
            font-family: fontAwesome;
            @include respond-to('md'){
                font-size: 100px;
            }
        }//after

    }
}//button
