%transition {
    transition: all 0.5s ease !important;
}

%clear {
    content: " ";
    display: block;
    width: 100%;
    height: 1px;
    clear: both;
}

%ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}

%flex-center-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

%text-light {
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.57;
}

%text-regular {
    @extend %text-light;

    font-weight: 400;
}

%text-semibold {
    @extend %text-light;

    font-weight: 600;
}

%font-normal{
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
}

%ellipsis{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}



