footer{
    background: $color-footer;
    padding: 50px 0;
    p.header{
        font-size: em(18);
        line-height: em(23);
        padding:20px 0 7px 0;
        color:white;
        font-weight: bold;
    }
    .row{
        @include respond-to("md"){
            display: flex;
        }
        .col-md-3{
            @include respond-to("md"){
                align-self: flex-end;
            }
        }    
    }
    .footer-menu{
        display: flex;
        padding-bottom: 80px;
        &.row{
            padding: 0;
            margin: 0;
        }
        .divider{
            padding: 10px 0;
        }
        .col {
            width: 20%;
            padding:0 10px;
            &:first-of-type{
                padding-left: 0;
            }
            &:last-of-type{
                padding-right: 0;
            }
        }
        .c-soon{
            color: rgba(255, 255, 255, 0.5);
        }
        
    }//footer
    a{
        display: block;
        width:100%;
        padding: 5px 0;
        color: rgba(255, 255, 255, 0.5);
        &:hover, &:focus,&:visited{
            color:darken(white,10);
            padding: 5px 0;
        }
    }
    .share{
        a{
            color:white;
            display: inline-block;
            margin-right: 10px;
            width: auto;
        
            &:last-child{
                margin-right: 0;
            }
            .svg-inline--fa{
               color: white;
               font-size: em(23); 
               &:last-child{
                    margin-left: 0;
                }
            }
        }//a
    }//share
    .footer-logo{
        width: 149px;
        display: block;
        margin: 45px 0;
        @include respond-to ('md'){
            margin: 30px 0;
        }
    }
    .sub-menu{
        margin: 0 0 44px 0;
        padding: 0;
        list-style-type: none;
        @include respond-to('sm'){
            margin: 0 0 40px 0; 
        }
        li{
            padding: 0 20px;
            margin-bottom: 25px;
            float: left;
            border-right: 1px solid white;
            &:nth-child(3n+3) {  
                border:none;
            }
            &:nth-child(3n+1) {  
                clear: both; 
                padding-left: 0;
            }
            &:first-child{
                padding-left: 0;
            }
            &:last-child{
                border:none;
            }

            a{
               color:white;
               font-size: em(13);
               line-height: em(13);
               padding: 0;
            }

            @include respond-to('xxs'){
                padding: 0 15px;
            }
            @include respond-to('sm'){
                margin-bottom: 0;
                &:nth-child(3n+3) {  
                    border-right: 1px solid white;
                }
                &:nth-child(3n+1) {  
                    clear: initial; 
                    padding: 0 20px;
                }
                &:first-child{
                    padding-left: 0;
                }
                &:last-child{
                    border:none;
                }            
            }          
        }//li
        &:after{
            @extend .clearfix;
        }
    }//sub-menu
    .copy,.address{
        margin-top: 40px;
        color:white;
        font-size: em(12);
        line-height: em(25);
        color: rgba(255, 255, 255, 0.5);
    }
    .copy{
        margin-top: 70px;
        @include respond-to ('md'){
            margin-top: 40px;
        }
    }
    .address{
        margin-top: 10px;
        font-size: em(13);
        line-height: em(22);
        p{
            a{
                padding: 0;
            }
        }
    }
    #accordion-footer{
        .panel-default {
            &.panel{
                background: none;
                -webkit-box-shadow: none;
                box-shadow: none;
            }
            border:none;
            > .panel-heading {
                color: white;
                background: none;
                border: none;
                padding: 10px 0;
                h4.panel-title{
                    > a{
                        font-size:em(18);
                        &:hover,
                        &:focus{
                            text-decoration:none;
                            border:none;
                            outline: none;
                        }
                        .svg-inline--fa{
                            font-size:13px;
                        }
                        > .fa-plus{
                            display: none;
                        }
                        > .fa-minus{
                            display: block;
                        }

                        &.collapsed{
                            > .fa-plus{
                                display: block;
                            }
                            > .fa-minus{
                                display: none;
                            }
                        }
                    }//a
                }//panel-title
                + .panel-collapse > .panel-body {
                    border: none;
                    padding: 0;
                    padding-bottom: 20px;
                    a{
                        font-size:em(16);
                    }
                }//panel-body
            }//panel-heading
        }//panel-default
    }//accordion-footer
}//footer