.f-005{
    .actions{
        padding-top: 80px;
        ul{
            list-style-type: none;
        }//ul
        p{
            color:$color-black-alpha;
        }
        strong{
            color:black;
        }
        &:first-child{
            padding-top: 0;
        }
        .subhead{
            margin:20px 0;
        }
        .panel-cta{
            padding-top: 30px;
            &:first-of-type{
                padding-top: 0;
            }
            display: block;
            &:after,
            &:before{
               @extend %clear ;
            }
            .info{
                margin-bottom: 50px;
                .ctas{
                    
                }
            }
        }//panel-cta
        .ctas{
            padding-top: 20px;
            padding-bottom: 20px;
            text-align: left;
            &.add{
                .btn{
                    width: 100%;
                    @include respond-to("sm"){
                        min-width: 168px;
                        width: auto;
                    }
    
                }//btn
            }//add
            @include respond-to("sm"){
                text-align: right;
            }
            @include respond-to("md"){
                text-align: right;
                padding-top: 0px;
                padding-bottom: 0px;
            }
            .btn{
                margin-bottom: 10px;
                width: 100%;
                @include respond-to("sm"){
                    min-width: 168px;
                    width: auto;
                }
                
                
            }
        }//ctas
        hr{
            margin-top: 30px;
            margin-bottom: 50px;
            @include respond-to("md"){
                margin-top: 20px;
                margin-bottom: 20px;
            }
        }
        #resend{
            cursor: pointer;
            display:block;
            @include respond-to("sm"){
                margin-top:25px; 
                text-align: right;
            }
        }
    }//actions
    &#moreAboutYou{
        .ctas{
            padding-top: 0; 
            padding-bottom: 0; 
            @include respond-to("sm"){
                text-align: right;
            }
            @include respond-to("md"){
                padding-top: 20px; 
                padding-bottom: 20px; 
            }
            .btn{
                min-width: 100%;
                @include respond-to("sm"){
                    min-width: 168px;
                }
            }
        }//ctas
        .actions{
            padding-top: 40px;
            @include respond-to("sm"){
                padding-top: 80px;
            }
            hr{
                margin-bottom: 30px;
            }
        }//actions
        .info{
            padding-bottom: 20px;
        }
        form{
            padding-top: 0;
            @include respond-to("md"){
                padding-top: 50px;
            }
        }//form
        .remove{
           background: none;
           &:hover{
            background: none;
           }
           a{
               color:$color-blue;
               &:hover{
                   text-decoration: underline;
               }
           }
        }//remove
    }
    .representative{
        ul{
            margin:0;
            padding: 0;
            &.af{
                li{
                    margin: 0;
                    a{
                        line-height: 23px;
                    }
                }//li
            }//af
        }//ul  
        hr{
            margin-top: 25px;
            margin-bottom: 40px;
        } 
        .info{
            margin-bottom:40px;
            &:after,
            &:before{
                @extend %clear;
            }
            .ctas{
                margin-top: 10px;
            } 
        }
        .resources{
            p{
                margin:0;
            }
            .eye-brow{
                font-size: 12px;
                color:$color-blue;
                margin-top: 0;
                margin-bottom: 5px;
                text-transform: uppercase;
                line-height: 1.33;
                letter-spacing: 1px;
            }
            .item{
                margin-bottom: 40px;
                &:after,
                &:before{
                    @extend %clear;
                }
                img{
                    margin-bottom:10px; 
                }
            }
        }//resources
        
    }//Representative
    .modal{
        .btn{
            padding: 10px 20px;
        }
    }
}//f-005

.panel-default.services{
    margin: 20px 0;
    @extend .box-shadow-complete;
    .content{
        padding:20px 10px;
        &:before,
        &:after{
            display: table;
            content: " ";
            clear: both;
        }
        h3{
            margin:0;
            padding: 0;
        }
        @include respond-to('md'){
            padding:25px;
        }
    }//content
    .panel-heading{
        background: none;
        padding: 0;
        h3{
            font-size: 18px;
            @include respond-to("sm"){
                line-height: 52px;
            }
            @include respond-to("lg"){
                font-size: 21px;
            }
            
        }
        img{
            padding-top: 16px;
            padding-bottom: 16px;
            @include respond-to("md"){
                padding-bottom: 0;
            }
        }
    }
    .panel-body{
        background: none;
        padding: 0;
        h3{
            padding-bottom: 30px;
            @include respond-to("xxs"){
                font-size: 18px;
            }
        }
        a{
            display: inline-block;
            width: 100%;
            padding:5px 0; 
        }
    }//body
    
    .btn{
        font-size: 14px;
        padding: 15px 10px;
        @include respond-to("lg"){
            font-size: 14px;
            padding: 15px 30px;
        }
    }
    
}

