form{
    padding-top: 50px;
    .dob-error{
        @extend %form-errors;
        display: none;
    } 
    .warning{
        font-size:14px;
        line-height: 20px;
        color: darken($color-blue,10%);
        display: none;
        padding:5px 0;
    }    
}//form

.groups.OtherEntity{
    display: none;
}
.showpass{
    cursor:pointer;
    font-size:14px;
}


.form-group{
    i.tooltipMark{
        font-size:20px;
        padding-left: 5px;
        cursor:pointer;
    }
    
    label{
        @extend %font-normal;
        @extend .text-alpha;
        &.error{
            @extend %form-errors;
            padding-bottom: 0;
         }
    }
    input.form-control:not(.btn),
    select.form-control:not(.btn){
        background: $color-gray;
        border:none;
        box-shadow:none;
        height: 40px;
        outline: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        border-radius: 3px;
        &:focus {
            border: 2px solid $color-blue;
        }
        &.error, &.errors{
            border: 2px solid $color-red !important;
            border-radius: 3px;
            &:focus {
                border: 2px solid $color-red !important;
            }
        }
    }//input
    select{
        -webkit-appearance: none;
        -moz-appearance:    none;
        appearance:         none;
        position: relative;
        background: $color-gray url("../images/chevron_down-10x10.png") no-repeat right 5px center !important;
        option{
            background: white;
            &:hover{
                background: black;
            }
        }
        @include respond-to('xxs'){
            font-size: 11px;
            padding: 6px !important;
        }
        &:focus{
            border:none !important;
        }
        &.select-big{
            font-size:26px;
            height: 60px !important;
            margin-bottom: 10px;
            color:rgba(black,.3);
            option {
                font-size: 15px;
                color:black;
            }
        }//big
    }//select
    .input-big{
        font-size:26px;
        height: 60px !important;
        margin-bottom: 10px;
        color:rgba(black,.3);
    }

    textarea {
        resize: none;
        background: $color-gray;
        border:none;
        -webkit-appearance: none;
        -moz-appearance:    none;
        appearance:         none;
        box-shadow: none;
        &:focus {
            border: 2px solid $color-blue;
        }
      }
}

.form-control.error{
    outline: 0;
    border: 2px solid $color-red;
    &:hover,
    &:focus{
        outline: 0;
        border: 2px solid $color-red;   
        box-shadow: none; 
    }
}



label{
    &.error{
       @extend %form-errors;
    }
}//label

.server-errors{
    display:none;
    span{
        @extend %form-errors;
    }
}

//custom-checkbox and radio
.custom-checkbox,
.custom-radio {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
    }
  }
  label.custom-checkbox.blue + p {
    text-indent: 0px;
    padding-left: 40px;
  }  
.custom-radio{
    .checkmark {
        border-radius: 50%; 
    }
}
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 24px;
    width: 24px;
    background-color: #eee;
    border-radius: 5px;
    border:2px solid darken($color-gray-plain, 20%);
  }
  .custom-checkbox,
  .custom-radio {
    &:hover input ~ .checkmark {
      background-color: #ccc;
    }
    &.blue{
        input:checked ~ .checkmark {
            background-color:$color-blue;
            background: $color-blue url('../images/path-12x12.png') 50% 50% no-repeat;
            border:none;
        }
    }//blue
  }

  
  .custom-radio {
    display: inline-block !important;
    vertical-align: super;
    &.blue{
        input:checked ~ .checkmark {
            background: #eee;
            border:2px solid darken($color-gray-plain, 20%);
            &:after{
                content: " ";
                width: 14px;
                height: 14px;
                display: block;
                border-radius: 50%;
                position: relative;
                background: $color-blue;
                margin: 3px;
            }
        }
    }//blue
  }
//custom-checkbox and radio


.select-error{
    display: none;
    span.error{
        @extend %form-errors;
        padding-bottom: 0;        
    }
}


.form-nav{
    color:black;
    font-size:12px;
    font-weight: 700;
    i{
        padding-right: 10px;
    }
    span{
        font-family: $font-family;
    }
}


  