.attribution-wrapper{
    color: $color-black-alpha;
    p{
        margin:0;
        padding: 0;
        font-weight: 700;
        padding: 12px 10px 0 0;
        width: auto;
        float: left;

        &.date{
            color: $color-black-alpha;
        }
        &.by{
            color:black;
        }
        &.text-small{
            float: left;
            margin-top: 0;
            display: inline-block;
            margin-right: 5px;
            color:rgba(102,102,102,0.7) !important;
        }
        
    }
    .attribution{
        padding-top: 15px;
        padding-bottom: 0;
        @include respond-to("md"){
            padding:5px 0; 
        }
    }
    .addthis_inline_share_toolbox {
        float: right;
        width: auto;
        clear: none !important;
       
    }
    /*.share{
        margin:0;
        padding:0;
        text-align: left;
