            text-align: right;
        }
        
        > div[class*="col-"]{
            padding: 0;
        }
        p {
            color: rgba(black, 0.4);
            display: inline;
        }
        #atstbx{
            .at-share-btn-elements{
                a{
                    background: none !important;
                }//a
            }//
            .at-icon-wrapper{
                &:first-child{
                    margin-left: 0;
                    padding-left: 0;
                }
            }
            .at-icon {
                fill: rgba(102, 102, 102, 0.7) !important;
                border: 0;
            }//at-icon
        }//atstbx
    }//share*/
   


    
    &.bg-dark-blue{
        color:white;
        p{
            font-weight: normal;
            &.date{
                color:$color-white-alpha;
            }
            &.by{
                color: white;
                
            }
        }
        .share{
            color:$color-white-alpha;
            p{
                color:$color-white-alpha; 
            }
            #atstbx{
                .at-icon {
                    fill: $color-white-alpha !important;
                    border: 0;
                }
            }//atstbx
        }//share
    }//bg-dark-blue
}//attribution



