.c-011{
    @extend .c-005;
    a{
        display: table;
        > div[class*="col-"]{
            padding: 0;
            background: white;
            display: table-cell;
            float:none;
            text-align: left;
            vertical-align: middle;
        }//div
        > div {
            &.text{
                @extend .box-shadow-complete;
                p{
                    color:black;
                    padding: 15px;
                    margin: 0;
                    font-weight: 700;
                    @include respond-to("md"){
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        width: 190px;
                    }
                    @include respond-to("lg"){
                        white-space: nowrap;
                        width: auto
                    }
                }//p
            }//text
        }//div
    }//a
}//c-011




