.login{
    margin-top: 3px;
    background: white;
    @include respond-to('md'){
        background: $color-gray;
    }
    .subhead{
        padding-bottom: 40px;
    }
    
    form{
        padding-top: 0;
        label{
            font-size: 13px;
        }
        i{
            vertical-align: middle;
        }
    }
    .panel{
        @extend %panel-form;
        .panel-body{
            padding: 0;
            @include respond-to('md'){
                padding: 15px;
            }
        }
        p{
            margin: 0;
            padding: 0;
            &.createAccount{
                padding: 40px 0 30px 0;
                a{
                    color:$color-blue;
                    &:hover{
                        color: darken($color-blue,15);
                    }
                }
            }
        }//p
    }
    .actions{
        //padding-top: 10px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        a{
            font-size: 14px;
            
            color: $color-black-alpha;
            @extend %transition;
            @include respond-to("md"){
                margin-right: 20px;
            }
            @include respond-to("lg"){
                margin-right: 40px;
            }
            &:last-child{
                margin-right: 0;
            }
            &:hover{
                color: black;
            }
        }
    }//actions
    
    
    .form-group{
        padding-bottom: 15px;
        label{
            padding-bottom: 15px;
            @extend %font-normal;
            @extend .text-alpha;
            &.error{
                @extend %form-errors;
                padding-bottom: 0;
             }
        }
        input{
            background: $color-gray;
            border-radius: 0;
            border:none;
            box-shadow:none;
            height: 40px;
        }
    }
    
    .col-md-4{
        width:100%;
        @include respond-to("md"){
            width:33%;
        }
    }
}//loin