.calcs{
    &.bg-blue{
        h1,h2,h3,.big-title,label, .form-group label,.eye-brow, span{
            color:white !important;
        }
        p{
            color:white
        }
    }
    .eye-brow{
        font-size: 14px;
    }
    .subhead{
        margin: 0;
    }
    label{
        padding-bottom: 10px;
        &.select{
            font-size:13px;
        }
    }
    div[class^="side_"]{
        padding-top: 40px;
    }
    .side_2{
        text-align: center;
        @include respond-to("md"){
            text-align: left;
        }
    }
    .big-title{
        font-size:30px;
        margin:10px 0;
        line-height: 1.2;
        @include respond-to("md"){
            font-size:60px;
            margin:0;
        }
    }

    .form-group {
        margin-bottom: 30px;
        @include respond-to("md"){
            margin-bottom: 15px;
        }
    }
   
}//calcs