//-------------------------------------------------------------------------------------------
#securedBody{
    > div{
        
        @include respond-to("md"){
            padding: 27px 45px 15px 45px;
        }
    }
    .row{
        clear: both;
    }
   table{
        border:none;
        margin: 0 0 18px;
        tr{
            td{
                padding: 10px 25px;
                vertical-align: top;
                border: none;
            }
        }
   }
   > p{
        &:first-of-type{
            > img{
                margin-bottom: 20px;
                width: 100% !important;
                height: auto !important;
            }
        }
    }//p
}//securedBody
//-------------------------------------------------------------------------------------------
.col-2-content{
    margin: 30px auto;
    .content-wrapper {
        padding: 30px;
        //border-top: 3px solid #5c6f7c;
        &.no-border{
            border-top:none;
        }
        > p{
           
            &:first-of-type{
                > img{
                    margin-bottom: 20px;
                    width: 100% !important;
                    height: auto !important;
                }
            }
        }//p
    }//div
    img{
        height: auto !important;
    }
    p{
        color: #5c6f7c;
    }
   .supporting-content-item{
        margin: 30px 0;
        border-top: 3px solid #5c6f7c;
        padding: 20px;
        background: white;
        width: 100%;
        ul{
            li{
                margin:5px 0;
                a{
                    color:$color-blue;
                    font-size: 1.35rem;
                    line-height: 1;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }//li    
        }//ul
        
    }//supporting-content-item    
}//col-2-content