.c-026{
    h2.header{
        padding-bottom: 40px;
        text-align: center;
        @include respond-to('md'){
            padding-bottom: 60px;
        }
    }
    .left-panel{
        padding:0;
    }
    
    .right-panel{
        padding:0;
        a{
            color:black;
        }
        .item{
            margin-bottom: 30px;
        }
        p{
            letter-spacing: -1px;
        }
        .genre{
            @extend %genre;
            margin-top:0;
        }
    }//right-panel 
    .card{
        margin-bottom: 60px;
        @include respond-to('md'){
            margin-bottom: 0;
        }
        h3{
            @extend %font-normal;
            font-size: 18px;
            font-weight: normal !important;
            line-height: 1.28;
            @include respond-to('md'){
                font-size: 24px;
                font-weight: 300 !important;
                line-height: 1.33;
                letter-spacing: -0.4px;
            }
        }
        p{
            color:$color-black-alpha;
        }
    }//card
    
}//c-026
