.c-30{
    > div[class*="col-"] > span > div[class*="col-"]{
        position: relative;
        @include respond-to('md'){
            min-height: 560px;
            display: flex;
            flex-wrap: wrap;
            align-content: center;
        }
    }
}//c-30

.scWebEditInput.scEnabledChrome:after{
    display:none;
}

.panel-group.accordion .panel-default.scEnabledChrome>.panel-heading .panel-title span.collapsed:after {
    content: "\f068" !important;
}

li.dropdown.mega-dropdown .scWebEditInput {
    width: auto;
}

// rich text styles
%editor-btn{
    display: inline-block;
    min-width: 104px;
}

%btn-small{
    font-size: 1.125rem;
    padding: 10px 20px;
    &:hover{
        font-size: 1.125rem;
        padding: 10px 20px;
    }
}
%btn-large{
    @extend .btn-large;
    margin-bottom: 0;
    &:hover{
        margin-bottom: 0;
    }
}

$colors:blue, aquamarine,cyan,orange,gray, darkblue;
$size: small,normal,large;
@each $current-size in $size {
    @each $current-color in $colors {
        //@debug $current-color;
        .editorbtn#{$current-color}#{$current-size},
        .editorbtn#{$current-color}#{$current-size}disabled{
            @extend %btn;
            @extend %editor-btn;
            @extend %btn-#{$current-size} !optional;
            @if $current-color == darkblue{
                @extend .btn-dark-blue;
            }@else{
                @extend .btn-#{$current-color} !optional;
            }    
        }
        
        .editorbtn#{$current-color}#{$current-size}disabled{
            @extend %disabled;  
        }
    }//each
}


/*links*/
.link, .linkbold, .linkcomment, .linkreply, .linkprogression{
    color:#005daa;
    cursor: pointer;
    &:hover{
        color:#005daa;
        text-decoration: underline;
    }
}
.linkbold{
    font-weight: bold;
}
.linkcomment{
    color:black;
    &:hover{
        color:black;
    }
}
.linkreply{
    color:rgba(196, 196, 196, 0.8);
    &:hover{
        color:rgba(196, 196, 196, 0.8);
    }
}
.linkprogression{
    color:black;
    text-transform: uppercase;
    &:before{
        content:"\f053";
        float:left;
        margin-right: 5px;
        font-family: FontAwesome;
        display: block;
    }
}