.c-007{
  h2{
      text-align: center;
  }   
    .slider{
        .slick-dots{
            li{
                button{
                    background: rgba(255, 255, 255, 0.5);
                    &:hover{
                        background: white;
                    }
                }            
                &.slick-active{
                    button{
                        background: white;
                    }//button
                }//slick-active
            } //li
        }//slick-dots
        .slick-arrow{
            top:40%;
        }//.slick-arrow 
    }//slider
    .card{   
        @extend %card;
        background: white;
    }
    .two-product-card-carousel{
        @include respond-to('sm'){
            width: calc(331px * 2);
            margin:50px auto;
        }
        @include respond-to('md'){
            width: calc(315px * 2);
            margin:50px auto;
        }
        @include respond-to('lg'){
            width: calc(379px * 2);
            margin:50px auto;
        }
    }
    &.slider-section{
        &.bg-gray{
            .slider{
                @extend %slider-gray-dots;
            }
            .slick-list {
                padding:5px 10px;
            }
           
        }//bg-gray
    }//slider-section
    &.bg-blue{
        .header{
            color:white;
        }
        .subhead{
            color:$color-white-alpha;
        }
    }//bg-blue
}//c-007


