section{
    &.c-004 {
        padding-bottom: 100px;
        position: relative;
        @include respond-to('md'){            
            padding-bottom: 0;
        }
        .carousel-image img {
            @include respond-to("md"){
                min-height: 631px;
            }
            
        }
        .container-fluid{
            max-width: 1440px;
            padding: 0;
        }//fluid
        .carousel-indicators{
            left: initial;
            width: 100%;
            margin-left: 0;
            bottom: 0;
            margin: 0;
            background: white;
            padding-bottom: 60px;
            li{
                background: lighten($color-gray-plain,10%);
                border:none;
                &.active{
                    background: $color-footer;
                }
            }//li

            @include respond-to('md'){
                display: none;
            }
        }
        .carousel-caption{
            position: relative;
            left:0;
            right: 0;
            width: 100%;
            color: black;
            font-size: 36px;
            padding:40px 50px;
            text-shadow: none;
            background: white;
            bottom:0;
            letter-spacing: -0.6px;
            text-align: left;
            line-height: 1.11;
            a{
                color: black;
            }
            @include respond-to('xxs'){
                font-size: 26px;
            }
            @include respond-to('md'){
                display: none;
            }
        }
    }//c-004
}//section

#tabbedHero {
    padding:0px;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    //height:50%;
    & > .row{
        float: none;
        width: 100%;
        margin-left: 0;
        margin-right: 0;
       
        @include respond-to('md'){
            width: 65%;
            float: right;
        }
    }
    @include respond-to('md'){
        height:630px;
        width: 100%;
    }
}
#tabbedHero .banner-float{
    width: 100%;
    @include respond-to('md'){
        width: 35%;
        float: left;
        display: block;
    }
}
#tabbedHero .nav a {
    border-radius:0px;
}

#tabbedHero .carousel-inner > .item {
    padding:0;
    background-image: none;
    @include respond-to('md'){
        //height:100%;
        height:630px;
        background-size: cover;
        background-position: center center;
    }
    .carousel-caption{
        cursor: pointer;
    }
}

#tabbedHero .carousel-inner > .item > .carousel-image {
    a{
        display: block;
        width: 100%;
        padding-bottom: 0;
        
        @include respond-to('md'){
            height: 630px;
            img{
                display: none;
            }
        }
    }
}
#tabbedHero .carousel-nav {
    height: 100%;
    display: none;
    @include respond-to('md'){
        display: block;
        .nav{
            height:630px;
        }
    }
}

#tabbedHero .carousel-nav .banner-pills{
    width: 100%;
    overflow: hidden;
    .nav-stacked{
        margin:0;
        padding: 0;
        >li{
            height: 33.33%;
            margin: 0;
            padding: 0;
            &:nth-child(2){
                border-top:1px solid lighten($color-gray-plain, 10%);
                border-bottom:1px solid lighten($color-gray-plain, 10%);
            }
            a{
                color:black;
                display: block;
                font-size:26px;
                font-weight: 300;
                height: 100%;
                @extend %font-normal;
                @extend %transition;
                align-items: center;
                display: flex;
                padding: 0 50px;
                line-height: 1.22;
                &:hover, &:focus{
                    background: $color-blue;
                    color:white;    
                }
                @include respond-to('lg'){
                    font-size: 36px;
                }
            }//a
            &.active{
                border:none;
                > a{
                    background: $color-blue;
                    color:white;
                }
            }//active
        }//li    
    }//nav-stacked
}//banner-pills



