.c-35{
    .subhead{
        color:black;
        width: 100%;
        font-weight: bold;
    }
}//c-35

.list-container{
    padding: 40px;
    //margin:20px 0;
    @extend .bg-gray;
    p, li,a{
        font-size:21px;
    }
    a{
        margin:5px 0;
        padding: 0;
        display: inline-block;
    }
    > ul > li > a{
        color:$color-blue;
        display: initial;
        &:hover{
            color:darken($color-blue, 10);
        }
    }
}//list-container