.c-013{
    > .container{
        > .row{
            display: flex;
            flex-wrap: wrap;
            > .col-xs-12.col-sm-6.col-md-4{
                display: flex;
            }
        }//display
    }//container
    .title{
        color: $color-black-alpha;
        padding-left: 35px;
        padding-bottom: 20px;
        @extend %font-normal;
        font-size: 21px;
        line-height: 1.57;
    }
    
    .card{
        @extend %card;
        margin:0;
        margin-bottom: 30px;
        width: 100%;
        position: relative;
        background: white;
        max-width: 100%;
        .badge{
            background: $color-footer;
            color:white;
            position: absolute;
            left: 0;
            top: -15px;
            margin: 0 auto;
            right: 0;
            width: 40%;
            text-transform: uppercase;
            padding: 8px 0;
            border-radius: 15px;
            @extend %font-normal;
            font-size: 12px;
            line-height: 1.33;
            letter-spacing: 1px;
        }
        ul{
            margin-bottom: 0;
            padding-bottom:0;
            &.bullets{
                margin-bottom: 0;
                padding-bottom:0;
            }
        }
    }//card
    &.bg-gray{
        .text-lined span{
            background: $color-gray;
        }
    }
    &.t-006{
        .card{
            max-width: 100%;
            
            .img-fit{
                @include respond-to("sm"){
                   height: 184px;
                }
            }
            .cta{
                margin: 20px 0;
                a{
                    @include respond-to("sm"){
                        padding: 15px 10px;
                    }
                    @include respond-to("md"){
                        padding: 15px 0;
                    }
                    @include respond-to("lg"){
                        padding: 15px 30px;
                    }
                }
            }
        }//car
        .btn{
            font-size: 13px;
            padding: 10px 5px;
            @include respond-to("sm"){
                font-size: 13px;
                padding: 10px 5px;
            }
            @include respond-to("md"){
                font-size: 15px;
                padding: 10px !important;
            }
        }//btn
    }//t-006
}//c-013