.c-014{
    @extend %content-carousel;
    .slider{
        .card{  
            max-width: 299px;
            margin: 0px 20px 0 0;
            @include respond-to('xxs'){
                max-width: 270px; 
            }
            
            @include respond-to('sm'){
                max-width: 320px;  
            }
            @include respond-to('md'){
                margin: 0px 30px 0 0;
                max-width: 213px;
            } 
            @include respond-to('lg'){
                margin: 0;
                max-width: 286px;
            }         
        }//card  
    }//slider
    &.pagingInfoSlider{
        color:$color-black-alpha;
    }
}//c-014

