%border{
    border-top: 1px solid lighten($color-gray-plain, 10%);
    border-bottom: 1px solid lighten($color-gray-plain, 10%);
}

.section-promotional{
    span.from{
        font-size: 13px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;                      
        line-height: 1.77;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;
        color:$color-black-alpha;
        display: block;
        padding-bottom: 20px;
    }
    .promotion-container{
        //border-top: 1px solid lighten($color-gray-plain, 10%);
        //border-bottom: 1px solid lighten($color-gray-plain, 10%);
        //padding: 20px 0;
        .promotion{
            padding: 25px 30px;
            .row {
                text-align: center;
                @include respond-to("md"){
                    text-align: left;
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                }
                
                .partner{
                    img{
                        max-width: 100%;
                        margin: 50px 0;
                        @include respond-to("md"){
                            margin: 0;
                        }
                    }
                }//partner
            }//row
            h6{
                font-size: 20px;
                font-weight: 300;
                font-style: normal;
                font-stretch: normal;         
                line-height: 1.25;
                letter-spacing: -0.3px;
            }
            .btn{
                padding:11px 30px;
                font-size: 15px;
                font-weight: normal;    
                font-style: normal;
                font-stretch: normal;
                line-height: 1.23;
            }
            span{
                font-size: 12px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;          
                line-height: 1.5;
                display: block;
                &.number{
                    font-size: 20px;
                    font-weight: bold;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: 0.9;
                    letter-spacing: normal;
                    text-align: right;
                    display: block;
                }
            }//p
            .button-wrapper{
                margin-top:30px;
                @include respond-to("md"){
                    text-align: right;
                }
            }//button
            
            .condition{
                font-size: 10px;
                font-weight: 300;
                font-style: normal;
                font-stretch: normal;          
                line-height: 1.5;
                margin-top: 15px;
                display: block;
                @include respond-to("md"){
                    text-align: right;
                }
            }
            .bottom{
                align-self: flex-end;
            }
        }//promotion
        &.bg-white{
            h6{
                color: black !important;
            }
            span,.condition{
                color:$color-black-alpha;
            }
            a.btn{
                @extend .btn-blue;
                color:$color-blue;
            }
            .number{
                color:$color-blue;
            }
        }//bg-white
        &.bg-blue{
            h6{
                color: white;
            }
            span,.condition{
                color:$color-white-alpha;
            }
            div > a.btn{               
                color:$color-blue;
            }
            .number{
                color:white;
            }

        }//bg-white

    }//promo-container
    &.bg-gray .promotion-container .promotion{
        h6{
            padding: 0;
            font-family: "Open Sans";
        }
        .number{
            color:$color-blue;
        }
        .left-panel{
            max-width: 22%;
        }
        .right-panel{
            padding:10px;
            display: flex;
            flex-wrap: wrap;
            .col-md-4{
                display: flex;
                flex-wrap: wrap;
            }
        }

    }
    &.logolockup{
        &:not(.section-normal){
            padding: 0;
        }
        
        &.bg-gray{
            .promotion-container{
                margin: 20px;
                padding: 20px 0;
                background: transparent;
                @extend %border;
                .promotion{
                    padding: 0;
                    display: flex;
                    flex: row wrap;

                }
            }//promo-contaier    
        }//bg-gray
        .promotion-container .promotion{
            padding: 0 30px;
            .row{
                h6{
                    text-align: left;
                }
                .number{
                    text-align: center;
                    @include respond-to("md"){
                        text-align: right;
                    }
                }
                span{
                    text-align: left;
                    margin: 0;
                    padding: 0;
                    @include respond-to("md"){
                        margin: 5px 0;
                        padding: 5px 0;
                    }
                }//p
                .top{
                    text-align: center;
                    margin: 50px 0 20px 0;
                    @include respond-to("md"){
                        text-align: right;
                    }
                }
                .bottom{
                    text-align: center; 
                    margin: 30px 0;
                    @include respond-to("md"){
                        text-align: right;
                        margin: 20px 0;
                    }
                
                    img{
                        display: inline-block;
                    }
                    .condition{
                        margin-top: 30px;
                        text-align: center;
                        @include respond-to("md"){
                            text-align: right;
                            margin-top: 5px;
                        }
                    }
                }//bottom
            }//row
        }//promotion
    }//logolockup
    &.two-columns{
        .eyebrow{
            font-size: 13px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.69;
            letter-spacing: normal;
            display: block;
        }
        .headline{
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.25;
            letter-spacing: -0.3px;
            display: block;
        }
        .subcopy{
            font-size: 12px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.5;
            letter-spacing: normal;
            color:$color-black-alpha;
            display: block;
        }
        .container{
            @include respond-to("md"){
                display: flex;
                flex-wrap: wrap;
            }
            .left-panel{
                text-align: center;
                padding: 50px 25px;
                width: 100%;
                @include respond-to("md"){
                    width: 40%;
                }
                &.bg-blue{
                    color:white;
                    span{
                        color:white;
                    }
                }
                span{
                    &.number{
                        font-size: 56px;
                        font-weight: 300;
                        font-style: normal;
                        font-stretch: normal;
                        line-height: 0.71;
                        letter-spacing: -0.9px;
                        position: relative;
                        width: 155px;
                        margin: 0 auto 10px auto;
                        text-align: left;
                        display: block;
                        span{
                            font-size: 23px;
                            font-weight: normal;                          
                            font-style: normal;
                            font-stretch: normal;
                            line-height: 1.09;
                            letter-spacing: -0.4px; 
                            position: absolute;
                            top:0;
                            right: 0;
                            display: block;
                        }
                    }//number
                }//p
            }//left-panel
            .right-panel{
                padding: 50px;
                width: 100%;
                text-align: center;
                @include respond-to("md"){
                    width: 60%;
                    text-align: left;
                }
                span{
                    padding: 2px 0;
                }
                

            }//right-panel
        }//container
        &.rate{
            .container{
                .col-centered.col-md-9{
                    .promotion-container{
                        display: flex;
                        flex-wrap: wrap;
                        margin: 20px;
                        padding: 20px 0;
                        @extend %border;
                    }
                }//col
            }//container
        }//rate
        &.icon{
            .container{
                .col-centered{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                }
            }//container
            .left-panel{
                width: 96px;
                padding: 0;
                .icon-wrapper{
                    width:96px;
                    height: 96px;
                    border-radius: 50%;
                    background: $color-blue;
                    text-align: center;
                    margin: 0 auto;
                    position: relative;
                    img{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: 50px;
                        height: 50px;
                        margin-top: -25px;
                        margin-left: -25px;
                        
                    } 
                }
            }//left
            .right-panel{
                width: calc(100%-96px);
                padding: 0 50px;
            }           
        }//icon
        &.logos{
            .container{
                display: block;
            }
            span.subcopy{
                color:$color-white-alpha !important;
            }
            .col-centered{
                @include respond-to("md"){
                    display: flex;
                    flex-wrap: wrap;
                }
                .left-panel{
                    text-align: left;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: center;

                }
                .right-panel{
                    display: flex;
                    flex-flow: row wrap;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    align-content: center;
                    padding: 20px;
                    &:after {
                        content: "";
                        flex: auto;
                    }
                    .logo-wrapper{
                        width: 25%;
                        padding:10px;
                        text-align: center;
                        box-sizing: border-box;
                        > img{
                            width: 100%;
                            display: block;
                            max-width: 100%;
                            height: auto;
                        }
                    }//logo
                }
            }//container-wrapper
        }//logos
    }//.two-columns
    .img-full{
        > img{
            @extend .img-fit;
        }
    }
    .card{
        > img{
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
    .bottom{
        > img{
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
}//section-promotional

a[disabled="disabled"]{
    cursor: not-allowed;
    pointer-events: none;
    opacity: 0.5;
    color: gray; 
}




