.c-006{
    @extend %content-carousel;
    .slider{
        .slick-arrow{
            top:28%;
            &.slick-next {
                right: -55px;
            }
            &.slick-prev{
                left: -55px;
            }//prev
        }//.slick-arrow 
        .card{  
            box-sizing: border-box;              
            display: block;
            max-width: 299px;
            margin: 0px 20px 0 0;
            h3{
                @extend %font-normal;
                font-size: 18px;
                line-height: 1.28;
                text-align: left;
                font-weight: bold;
                @include respond-to('md'){
                    font-size: 30px;
                    line-height: 1.2;
                    text-align: left;
                    font-weight: 300;
                }
            }//h3

            @include respond-to('xxs'){
                max-width: 270px; 
            }
            
            @include respond-to('sm'){
                max-width: 320px;  
            }
            @include respond-to('md'){
                margin: 0px 30px 0 0;
                max-width: 285px;
            } 
            @include respond-to('lg'){
                margin: 0;
                max-width: 604px;
                //max-width: 50%;
            }         
        }//card 
    }//slider 
}//c-006

