.section-text-button{
    padding: 80px 0;
    &.bg-blue{
        h2{
        color:white
        }
        .subhead{
            color:$color-white-alpha;
            margin:30px auto 50px;
            text-align: center;
        }
    }//bg-blue
}

.section-promotional{
    &.bg-gray{
        p.from{
            text-align: center;
            padding-bottom: 20px;
        }
        .promotion-container{
            background:white;
            color:$color-green;
            border:none;
            .promotion{
                h6{
                    font-family: TimesNewRomanPSMT;
                    padding: 25px 50px;
                }
                h6,p{
                    color:$color-green;
                }
            }//promotion
        }//container
    }//bg-gray

}//section-promotional