.video-hero{
    background: $color-footer;
    color:white;
    padding: 50px 0 5px 0;
    div[class^="col-"]{
        padding-right: 0;
        padding-left: 0;
        @include respond-to("md"){
            padding-right: 15px;
            padding-left: 15px; 
        } 
    }//div
    
    iframe{
        height: 188px;
        @include respond-to("xxs"){
            height: 156px;
        }  
        @include respond-to("sm"){
            height: 388px;
        }   
        @include respond-to("md"){
            height: 375px;
        }
        @include respond-to("md"){
            height: 458px;
        }   
    }
}//video-hero