//font base is 16px transform into em use em() function send px and pt as parameter
h1,.big-title {
    color: black;
    font-size: em(36);
    @extend %text-light;
    .leading {
        font-size: em(64);
    }

    .tracking {
        font-size: em(63);
    }
    @include respond-to('md'){ 
        font-size: em(60);
    }
    &.title{
        line-height: 1.2;
    }

}

h2, .header {
    color: black;
    font-size: em(24);
    line-height: 1.35;
    margin: 10px 0;
    @extend %text-light;
    .leading,
    .tracking {
        font-size: em(44);
    }
    @include respond-to('md'){
        font-size: em(36);
    }
    
}//h2

h3 {
    color: black;
    font-size: 21px;
    line-height: 1.57;
    @extend %text-regular;

    &.leading,
    &.tracking {
        font-size: em(24);
    }
    &.subtitle{
        line-height: 1.5;
    }
    .c-soon{
        font-size:em(16);
    }
}

h4 {
    color: black;
    font-size: 18px;
    font-weight: bold;

    .leading,
    .tracking {
        font-size: em(23);
    }
}

h6 {
    color: black;
    font-size: 30px;
    line-height: 1.2;
    @extend %text-light;
    .leading,
    .tracking {
        font-size: 36;
    }
    &.header{
        font-size: 30px;
    }
}//h6

h7 {
    color: black;
    font-size: em(24);

    @extend %text-light;

    .leading,
    .tracking {
        font-size: em(32);
    }
}

.lead,.subhead{
    @extend %text-light;
    font-size: em(21);
    line-height: 33px;
    max-width: 902px;
    margin:35px auto;  
}

.subhead{
    @extend .lead;
    font-size: 1em;
    line-height: 1.44;
    @include respond-to("md"){
        font-size: 21px;
    }
}

.eye-brow{
    font-size:em(18);
    color:$color-blue;
}

.trade-mark{
    font-size:em(10);
    margin-right: 5px;
}

p{
    &.big-title{
        @extend h1;
    }
    &.check{
        text-indent: -0.75em;
        padding-left: 1.5em;
        i{
            padding-right: 20px;
            color:$color-aquamarine;
        }
    }
    @include respond-to("xxs"){
        font-size: 14px;
    }
}//p


a{
    color: $color-blue;
    text-decoration: none;
    padding-bottom:2px;
    @include respond-to("xxs"){
        font-size: 14px;
    } 
    &.bottom-section-link{
        @extend .lead;
        margin:initial;
        max-width: 100%;
        border-bottom: 1px solid $color-blue;
        &:hover{
            border-bottom: 1px solid darken($color-blue,20%);  
        }
    }
    &:hover{
        padding-bottom:2px; 
        outline: none;
        text-decoration: none;
       // border-bottom: 1px solid darken($color-blue,20%);
        color:darken($color-blue,20%);        
    }
    &.link-underline{
        text-decoration: underline;
        /*text-decoration: none;
        display: inline-block;
        border-bottom: 1px solid;
        &.black-alpha{
            color:$color-black-alpha;
        }*/
    }
    
}

.text- {
    &left {
        text-align: left;
    }

    &right {
        text-align: right;
    }

    &center {
        text-align: center;
    }

    &small {
        font-size: em(12);
        line-height: 1.44;
    }
    &alpha {
        color: rgba(0, 0, 0, 0.5) !important;
        &-white {
            color: rgba(255, 255, 255, 0.5) !important;
        }
    }//alpha
    

    &light {
        @extend %text-light;
    }
    &blue{
        color:$color-blue;
    }
    &white{
        color:white !important;
    }
    &black{
        color:black !important;
    }
    
    
}//text
