section{
    .content{
        p.vanish{
            background: -webkit-linear-gradient(black,black, #eee);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
    &.article{
        color:black;
        padding: 5px;
        @include respond-to('md'){
            padding: 40px 0;
        }
        p{
            color:black;
            padding:15px 0;
            a{
                color:black;
                text-decoration: underline;
                text-underline-position: under;
            }
        }//p
        
        img{
            display: block;
            max-width: 100%;
            height: auto;
            padding-bottom: 20px;
        }

        .article-image{
            @include respond-to('md'){
                padding: 30px 0;
            }
        }
        
        
        .read-more{
            color: black;
            text-align: center;
            position: relative;
            overflow: hidden;
            padding:50px 0;
            //margin-bottom: 100px;
            &:after,
            &:before {
                content: ' ';
                display: flex;
                background: lighten($color-gray-plain,15%);
                width: 100%;
                position: absolute;
                top: 50%;
                bottom: 0;
                width: 9600px;
                height: 1px;
                z-index: -1;
            }
            &:before{
                right: 57%;
            }
            &:after {
                left: 57%;
            }
            a{
                font-size:em(12);
                padding: 10px 30px;
            }
        }//read-more
        &.bg-dark-blue{
            *{
                color:white;
                p{
                    @extend %font-normal;    
                    font-weight: 200;
                }                
            }
        }
    }//article
    .article-image{
        padding: 0 0 40px 0;
    }
    .read-more{
       margin-bottom: 0;
    }
    .body-copy.part-2{
        margin-bottom: 0;
    }
}//section

