.duplicate-registration{
    @extend .registration;
    margin-top: 3px;
    background: white;
    @include respond-to('md'){
        background: $color-gray;
    }
    .panel{
        @extend %panel-form;
    }
    form{
        padding-top: 0;
        label:not(.error){
            text-align: left;
        }
    }
    .showpass{
        display: block;
        padding:5px 0;
    }
}//zip-verification

