.c-028{
    .row-grid{
        .top{
            padding-bottom: 50px;
        }
    }
    .items{
        //padding-top: 70px;
        //padding-bottom: 50px;
        [class*="col-"] {
            display: flex;
        }
        .card{
            @extend %card;
            max-width: 100%;
            width: 100%;
            box-shadow: none;
            padding:35px 40px;
            margin:0 0 30px 0;
            border-radius: 0;
            @include respond-to('md'){
                padding:70px;
            }
            h3{
                @extend %font-normal;
                font-size: 24px;
                font-weight: 300;
                line-height: 1.33;
                letter-spacing: -0.4px;
                @include respond-to('md'){
                    font-size: 36px;
                    letter-spacing: 0;
                    line-height: 1.22;
                }    
            }
            .btn{
                @include respond-to('md'){
                    width: 68%;
                }
            }
        }//card
    }//items
    &.cols-3{
        .card{
            padding: 35px;
            h3{
                font-size: 21px;
            }
            .cta{
                margin:40px 0 10px 0;
                .btn{
                    @include respond-to('md'){
                        width: 90%;
                        margin: 0 auto;
                    }
                }//btn
            }//cta
            
        }//card
    }//cols-3
}//c-28